<template>
  <div class="grid-content bg-purple">
    <el-carousel :interval="5000" arrow="hover">
      <el-carousel-item v-for="o in url" :key="o">
        <img :src="o" style="width:100%; height:100%">
      </el-carousel-item>
    </el-carousel>
  </div>
</template>

<script>
export default {
  data () {
    return {
      url: ['../../../static/images/banner/sw1.jpg', '../../../static/images/banner/sw2.jpg', '../../../static/images/banner/sw3.jpg']
    }
  }
}
</script>

<style scoped>
.grid-content {
  border-radius: 4px;
  min-height: 36px;
}
.el-carousel {
  border-radius:4px;
  display:block;
}
.el-carousel__item h3 {
  color: #475669;
  font-size: 18px;
  opacity: 0.75;
  line-height: 300px;
}
.el-carousel__item:nth-child(2n) {
  background-color: #99a9bf;
}
.el-carousel__item:nth-child(2n+1) {
  background-color: #d3dce6;
}
</style>



