<template>
  <div class="content">
    <div v-if="common_show">
      <v-content-up-carousel></v-content-up-carousel>
      <br/>
      <v-content-up-banner></v-content-up-banner>
      <br/>
      <v-scroll></v-scroll>
    </div>
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span><b>推荐作品</b></span>
        <el-button style="float: right; padding: 3px 0" type="text"><router-link :to="'/articleList?type=article'">更多</router-link></el-button>
      </div>
      <el-row>
        <el-col :span="articleSpan" v-for="(o) in article.data.slice(0, maxSize)" :key="o.id">
          <div class="imgbox">
            <router-link :to="'/article?id=' + o.id + '&type=article'"><img :src="o.img" class="image"></router-link>
            <div style="padding: 14px;">
              <div>{{ o.title }}</div>
              <span class="remark">{{ o.remark }}</span>
              <div class="bottom clearfix">
                <time class="time">{{ o.time }}</time>
                <el-button type="text" class="button"><router-link :to="'/article?id=' + o.id + '&type=article'">查看详情</router-link></el-button>
              </div>
            </div>
          </div>
        </el-col>
      </el-row>
    </el-card>
    <br/>
    <el-row :gutter="commonGutter">
      <el-col :span="docSpan">
        <el-card class="box-card">
          <div slot="header" class="clearfix">
            <span><b>技术文档</b></span>
            <el-button style="float: right; padding: 3px 0" type="text"><router-link :to="'/docList?type=doc'">更多</router-link></el-button>
          </div>
          <div v-for="o in doc.data.slice(0, maxSize)" :key="o.id" class="text item">
            <router-link :to="'/doc?id=' + o.id + '&type=doc'"> > {{ o.title }}</router-link>
            <span style="float: right;">{{o.time}}</span>
          </div>
        </el-card>
      </el-col>
      <el-col :span="videoSpan">
        <el-card class="box-card">
          <div slot="header" class="clearfix">
            <span><b>学习视频</b></span>
            <el-button style="float: right; padding: 3px 0" type="text"><router-link :to="'/vedioList?type=vedio'">更多</router-link></el-button>
          </div>
          <div v-for="o in vedio.data.slice(0, maxSize)" :key="o.id" class="text item">
            <router-link :to="'/vedio?id=' + o.id + '&type=vedio'"> > {{ o.title }}</router-link>
            <span style="float: right;">{{o.time}}</span>
          </div>
        </el-card>
      </el-col>
    </el-row>
    <br/>
    <el-row :gutter="commonGutter">
      <el-col :span="projectSpan">
        <el-card class="box-card">
          <div slot="header" class="clearfix">
            <span><b>开源项目</b></span>
            <el-button style="float: right; padding: 3px 0" type="text"><router-link :to="'/projectList?type=project'">更多</router-link></el-button>
          </div>
          <div v-for="o in project.data.slice(0, maxSize)" :key="o.id" class="text item">
            <router-link :to="'/project?id=' + o.id + '&type=project'"> > {{ o.title }}</router-link>
            <span style="float: right;">{{o.time}}</span>
          </div>
        </el-card>
      </el-col>
      <el-col :span="toolSpan">
        <el-card class="box-card">
          <div slot="header" class="clearfix">
            <span><b>开发工具</b></span>
            <el-button style="float: right; padding: 3px 0" type="text"><router-link :to="'/toolList?type=tool'">更多</router-link></el-button>
          </div>
          <div v-for="o in tool.data.slice(0, maxSize)" :key="o.id" class="text item">
            <router-link :to="'/tool?id=' + o.id + '&type=tool'"> > {{ o.title }}</router-link>
            <span style="float: right;">{{o.time}}</span>
          </div>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import vContentUpCarousel from '../component/ContentUpCarousel.vue'
import vContentUpBanner from '../component/ContentUpBanner.vue'
import vScroll from '../component/Scroll.vue'
import { article } from '../../../static/js/article.js'
import { tool } from '../../../static/js/tool.js'
import { vedio } from '../../../static/js/vedio.js'
import { doc } from '../../../static/js/doc.js'
import { project } from '../../../static/js/project.js'

export default {
  data () {
    return {
      maxSize: 6,
      currentDate: '2019-05-01',
      article: article,
      tool: tool,
      vedio: vedio,
      doc: doc,
      project: project,
      trigger: false,
      articleSpan: 8,
      docSpan: 12,
      projectSpan: 12,
      videoSpan: 12,
      toolSpan: 12,
      commonGutter: 16,
      fullWidth: document.documentElement.clientWidth,
      common_show: true
    }
  },
  components: {
    vContentUpCarousel, vContentUpBanner, vScroll
  },
  created () {
    window.addEventListener('resize', this.handleResize)
    this.handleResize()
  },
  beforeDestroy: function () {
    window.removeEventListener('resize', this.handleResize)
  },
  methods: {
    handleResize (event) {
      this.fullWidth = document.documentElement.clientWidth
      // 判断列数、组件显示
      if (this.fullWidth > 1500) {
        this.articleSpan = 8
        this.docSpan = 12
        this.projectSpan = 12
        this.videoSpan = 12
        this.toolSpan = 12
        this.common_show = true
      } else if (this.fullWidth > 1025) {
        this.articleSpan = 12
        this.docSpan = 12
        this.projectSpan = 12
        this.videoSpan = 12
        this.toolSpan = 12
        this.common_show = true
      } else {
        this.articleSpan = 24
        this.docSpan = 24
        this.projectSpan = 24
        this.videoSpan = 24
        this.toolSpan = 24
        this.common_show = false
      }
    }
  }
}
</script>

<style scoped>
.bg-purple {
  background: #d3dce6;
}
.box-card .img {
  width: 200px;
  height: 80px;
  overflow:hidden
}
.text {
  font-size: 14px;
}
.item {
  margin-bottom: 18px;
}
.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}
.clearfix:after {
  clear: both
}
/* 卡片图片 */
.time {
  font-size: 13px;
  color: #999;
}

.bottom {
  margin-top: 13px;
  line-height: 12px;
}

.button {
  padding: 0;
  float: right;
}

.image {
  width: 100%;
  height: 200px;
  display: block;
}

.clearfix:before,
.clearfix:after {
    display: table;
    content: "";
}

.clearfix:after {
    clear: both
}
.imgbox {
  background:white;
  margin: 5px;
  padding: 10px;
  border: 1px solid rgb(250, 244, 244);
}
.imgbox:hover{
  -webkit-box-shadow: 2px 2px 8px 1px rgba(20, 20, 20, 0.15);
  -moz-box-shadow: 2px 2px 8px 1px rgba(20, 20, 20, 0.15);
  box-shadow: 2px 2px 8px 1px rgba(20, 20, 20, 0.15);
}
.remark {
  margin-top: 10px;
  font-size: 12px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  /* autoprefixer: off */
  -webkit-box-orient:vertical;
  /* autoprefixer: on */
}
</style>



